@mixin gallery {

  /* === - (, ) === */
  .media {
    &__filters {
      display: flex;
      justify-content: center;
      gap: 10px;
      margin-bottom: 30px;

    }

    &__grid {
      display: flex;
      justify-content: space-between;
      gap: 30px;

      >* {
        flex: 1;
      }
    }

    &__card {
      background: #fff;
      border-radius: 8px 8px 16px 16px;
      overflow: hidden;
      cursor: pointer;
      transition: transform 0.2s ease, opacity 0.3s ease;
      opacity: 1;
      transform: translateY(0);
      border: 1px solid #e7e7e7;

      &:hover {
        transform: translateY(-3px);
      }

      &--hidden {
        opacity: 0;
        transform: translateY(10px);
        pointer-events: none;
      }
      .news__blog-list{
        margin-bottom: 16px;
      }
    }

    &__preview {
      position: relative;
      overflow: hidden;

      .media__img {
        width: 100%;
        height: 280px;
        border-radius: 8px;
        object-fit: cover;
        display: block;
        transition: transform 0.3s ease;
      }

      &:hover .media__img {
        transform: scale(1.05);
        border-radius: 8px;

      }

      .media__badge {
        position: absolute;
        top: 10px;
        right: 10px;
        font-weight: 600;
        font-size: 18px;
        line-height: 161%;
        letter-spacing: -0.01em;
        text-align: center;
        color: #fff;
        background: #2060f3;
        border-radius: 12px;
        padding: 5px 20px;

        &--video {
          background: #ef4444;
        }
      }

      &:hover .media__badge {
        opacity: 1;
        transform: translateY(0);
      }

      .media__play {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 56px;
        height: 56px;
        background: rgba(255, 255, 255, 0.9);
        border-radius: 50%;
        transition: background 0.3s ease;

        &::after {
          content: '';
          position: absolute;
          top: 50%;
          left: 54%;
          transform: translate(-50%, -50%);
          border-style: solid;
          border-width: 10px 0 10px 16px;
          border-color: transparent transparent transparent #111;
        }

        &:hover {
          background: #fff;
        }
      }
    }

    &__content {
      padding: 30px 16px 44px 16px;
    }

    &__title {
      font-weight: 700;
      font-size: 22px;
      color: #171717;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      margin-bottom: 12px;
    }

    &__text {
      font-weight: 400;
      font-size: 18px;
      color: #717171;
      line-height: 140%;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 3; //  3 
      overflow: hidden;
      text-overflow: ellipsis;
      margin-bottom: 24px;
    }

    &__link {
      font-weight: 600;
      font-size: 16px;
      display: flex;
      align-items: center;
      color: #3ea34b;
      gap: 10px;
    }
  }

  /* === - === */
  .filter-btn {
    font-size: 18px;
    line-height: 161%;
    letter-spacing: -0.01em;
    color: #fff;
    border-radius: 8px;
    padding: 8px 22px;
    background: #fff;
    border: 1px solid #e7e7e7;
    color: #171717;
    cursor: pointer;
    transition: all 0.3s ease;

    &.active {
      background: #3ea34b;
      color: #fff;
      border-color: #3ea34b;
    }

    &:hover {
      background: #e5e7eb;
    }
  }

  /* ===  === */
  .media-modal {
    display: flex;
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.8);
    z-index: 1000;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s ease;

    &.active {
      opacity: 1;
      pointer-events: auto;
    }

    &__content {
      position: relative;
      max-width: 80%;
      width: 900px;
      border-radius: 10px;
      transform: scale(0.95);
      transition: transform 0.3s ease;

      img,
      iframe {
        width: 100%;
        aspect-ratio: 16 / 9;
        border: none;
        border-radius: 10px;
        display: block;
      }
    }

    &__close {
      position: absolute;
      top: -40px;
      right: -10px;
      font-size: 2rem;
      color: #fff;
      cursor: pointer;
    }

    &.active &__content {
      transform: scale(1);
    }
  }

}

.media {
  .swiper {
    overflow: visible;
  }

  .swiper-pagination {
    bottom: -45px;
  }

}