@import "reset";
//  
@import "vars";
@import "mixins";
@import "settings";

//   
@import "./components/header";

.main {
  background: #f9fafb;
}

.tabs {
  @include tabs;
}

.btn {
  border-radius: 16px;
  transition: all .3s linear;
  padding: 20px 32px;
  font-weight: 600;
  font-size: 18px;
  letter-spacing: -0.01em;

  &:hover {
    transform: scale(1.03);
  }
}

.btn-green {
  background: #00a92a;
  color: #fff;

  &:hover {
    background: #218838;
    box-shadow: 0 4px 10px rgba(40, 167, 69, 0.3);
  }
}

.btn-white {
  background: #fff;

  &:hover {
    background: #E5E5E5;
    box-shadow: 0 4px 10px rgba(211, 211, 211, 0.3);
  }
}

.title {
  font-weight: 700;
  font-size: 42px;
  text-align: center;
  color: #171717;
  line-height: 130%;
}

.title--two {
  font-weight: 5 00;
  font-size: 46px;
  text-align: center;
  color: #171717;
  line-height: 130%;
}

.text {
  font-size: 18px;
  line-height: 161%;
  letter-spacing: -0.01em;
}

.btn-border {
  border: 2px solid #e7e7e7;
  border-radius: 16px;

  &:hover {
    box-shadow: 0 4px 10px rgba(211, 211, 211, 0.3);
  }
}

.swiper-button {
  width: 56px;
  height: 56px;
  border: 1px solid #eaeaea;
  border-radius: 12px;
  box-shadow: 0 4px 3px -1px rgba(0, 0, 0, 0.12);
  background: #fff;

  &::after {
    content: '';
    background: url(../img/arrow-right.svg) no-repeat;
    width: 10px;
    height: 17px;
    display: block;
    position: absolute;
  }

  &-prev {
    &::after {
      content: '';
      background: url(../img/arrow-left.svg) no-repeat;
      width: 10px;
      height: 17px;
      display: block;
      position: absolute;
    }
  }
}


@include gallery;

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
}

.swiper-pagination-bullet-active {
  background: #3EA34B;
}

.impact {
  width: 100%;
