  }

}

.hero {
  padding: 260px 0 51px 0;
  background:
    linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    /* 40%  */
    url('../img/hero.jpg') center/cover no-repeat;
  // height: 100vh;
  display: flex;
  text-align: center;

  &__inner {
    width: 100%;
    max-width: 835px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

  }

  &__title {
    font-weight: 700;
    font-size: 76px;
    line-height: 140%;
    text-align: center;
    color: #fff;
    letter-spacing: 3px;

