  }

  &__info {
    display: flex;
    gap: 40px;
    margin-bottom: 50px;

    &-item {
      display: flex;
      align-items: center;
      gap: 10px;

      span {
        display: inline-block;
        font-weight: 500;
        font-size: 22px;
        line-height: 132%;
        letter-spacing: -0.01em;
        color: #fff;
      }
    }
  }

  &__user {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 50px;

