  }

  &__buttons {
    display: flex;
    align-items: center;
    gap: 16px;
    margin-bottom: 93px;

    &-btn {
      font-size: 18px;
      font-weight: 600;
      letter-spacing: -0.01em;
      border: 2px solid transparent;
      padding: 16px 35px;

      &--border {
        color: #66db7e;
        border: 2px solid #66db7e;
      }
    }

  }
}

