      font-size: 18px;
      line-height: 161%;
      letter-spacing: -0.01em;
    }

    &-power {
      font-weight: 600;
      font-size: 24px;
      line-height: 121%;
      letter-spacing: -0.01em;
      color: #171717;
      margin-bottom: 12px;
    }

