      display: flex;
      align-items: center;
      gap: 24px;
    }

    &-btns,
    &-btn{
      border-radius: 16px;
      padding: 16px 32px;
    }

    &-right {
      border: 1px solid #e5e5e5;
      border-radius: 18px;
      padding: 30px 30px 0 30px;
    }

    &-titsl {
      font-weight: 600;
      line-height: 130%;
      font-size: 24px;
      margin-bottom: 12px;
      position: relative;
      padding-left: 30px;

      &::before {
        content: '';
        background: #3ea34b;
        display: block;
        width: 17px;
        height: 17px;
        position: absolute;
        left: 0;
        top: 7px;
        border-radius: 50%;
      }
    }

    &-liss {
      padding: 12px;
      margin-bottom: 12px;

      &:last-child {
        margin-bottom: 0;
      }

      p {
        margin-bottom: 0;
      }

      &:nth-child(1) {
        background: #f3fdf5;

        h4 {
          color: #2b6536;
        }

        p {
          color: #3ea34b;
        }
      }

      &:nth-child(2) {
        background: #f0f6fe;

        h4 {
          color: #2060f3;
        }

        p {
          color: #2060f3;
        }
      }

      &:nth-child(3) {
        background: #f9f6fe;

        h4 {
          color: #6322aa;
        }

        p {
          color: #8a2ff1;
        }
      }

      &:nth-child(3) {
        background: #f9f6fe;

        h4 {
          color: #6322aa;
        }

        p {
          color: #8a2ff1;
        }
      }

      &:nth-child(4) {
        background: #fef7ee;

        h4 {
          color: #943615;
        }

        p {
          color: #e45706;
        }
      }

      &:nth-child(5) {
        background: #f3fdfa;

        h4 {
          color: #245e5a;
        }

        p {
          color: #339489;
        }
      }

      &:nth-child(6) {
        background: #fcf3f3;

        h4 {
          color: #921f1c;
        }

        p {
          color: #d52322;
        }
      }

    }

    &-titl {
      font-weight: 600;
      font-size: 24px;
      line-height: 121%;
      letter-spacing: -0.01em;
    }
  }
