      position: absolute;
      opacity: 0;
      pointer-events: none;
    }

    &-box {
      width: 20px;
      height: 20px;
      border: 2px solid #d1d5db;
      border-radius: 6px;
      background: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.25s ease;
      flex-shrink: 0;

      &::after {
        content: "";
        width: 10px;
        height: 6px;
        border-left: 2px solid #fff;
        border-bottom: 2px solid #fff;
        transform: rotate(-45deg) scale(0);
        opacity: 0;
        transition: all 0.2s ease;
      }
    }

    &:hover &-box {
      border-color: #16a34a;
    }

    input:checked+&-box {
      background: #16a34a;
      border-color: #16a34a;

      &::after {
        transform: rotate(-45deg) scale(1);
        opacity: 1;
      }
    }

