@mixin tabs {
  .tabs__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    background: #ececf0;
    border-radius: 90px;
    padding: 4px;
    margin-bottom: 50px;
    > * {
      flex: 1;
    }
  }

  .tabs__nav-btn {
    width: 100%;
    padding: 10px;
  }

  .tabs__nav-btn--active {
    background: #fff;
    border-radius: 90px;
  }

  .tabs__panel {
    display: none;
  }

  .tabs__panel--active {
    display: block;
  }
}