.header {
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    background: #fff;
    padding: 21px 0 21px 0;
    z-index: 1000;

    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__logo {
        margin: 0 27px 0 0;

        &-link {
            display: inline-block;
        }
    }

    @include dropdown-menu;

    &__menu {
        position: relative;
        top: -1px;

        &-list {
            display: flex;
            align-items: center;
            gap: 36px;
        }

        &-link {
            font-weight: 600;
            font-size: 16px;
            letter-spacing: -0.01em;
        }

        .header__language {
            display: none;
        }

        .header__btn {
            display: none;
        }
    }


    &__language {
        position: relative;
        top: -1px;
        display: flex;
        align-items: center;
        gap: 9px;
        margin: 0 27px;

        &-item {
            border-right: 1px solid #171717;
            padding-right: 9px;

            &:last-child {
                border-right: none;
                padding-right: 0px;
            }
        }

        &-link {
            text-transform: uppercase;
            font-weight: 600;
            font-size: 16px;
            letter-spacing: -0.01em;
            transition: all .2s linear;

            &:hover {
                color: #00a92a;
            }
        }


    }

    &__btn {
        position: relative;
        top: -2px;
        font-weight: 400;
        color: #fff;
        font-weight: 400;
        font-size: 16px;
        letter-spacing: -0.01em;
        padding: 9px 17px !important;
    }
}













.footer {
    background: #111827;
    color: #fff;
    padding: 60px 0 30px;

    &__top {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
        gap: 20px;
        margin-bottom: 40px;
    }

    &__logo {
        margin-bottom: 37px;
    }

    &__desc {
        margin-bottom: 30px;
    }

    &__details {}

    &__detail {
        display: flex;
        align-items: center;
        gap: 12px;
        margin-bottom: 14px;

        &:last-child {
            margin-bottom: 0;
        }

        span {
            display: flex;
            width: 28px;
            justify-content: center;
        }
    }

    &__title {
        font-weight: 600;
        font-size: 22px;
        line-height: 132%;
        letter-spacing: -0.01em;
        color: #fff;
        margin-bottom: 16px;
    }

    &__list {
        list-style: none;
        padding: 0;

        li+li {
            margin-top: 16px;
        }

        a {
            font-weight: 400;
            font-size: 18px;
            line-height: 161%;
            letter-spacing: -0.01em;
            color: #fff;


            &:hover {
                opacity: 1;
            }
        }
    }

    &__person {

        margin-bottom: 30px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__name {
        font-weight: 600;
        font-size: 22px;
        line-height: 132%;
        letter-spacing: -0.01em;
        color: #00e165;
        margin-bottom: 8px;
    }

    &__contact {
        display: flex;
        align-items: center;
        gap: 10px;
        font-size: 18px;
        line-height: 161%;
        letter-spacing: -0.01em;
        color: #fff;
        margin-bottom: 8px;

        &:last-child {
            margin-bottom: 0;
        }

        &:hover {
            opacity: 1;
        }
    }

    &__text {
        margin-bottom: 10px;
    }

    &__form {
        display: flex;
        height: 51px;
        margin-bottom: 20px;
    }

    &__input {
        flex: 1;
        padding: 10px 14px;
        border: none;
        border-radius: 8px 0 0 8px;
        background: #1a2230;
        border-radius: 12px;
        color: #fff;
        margin-right: 10px;

        &::placeholder {
            font-weight: 400;
            font-size: 15px;
            line-height: 193%;
            letter-spacing: -0.01em;
            color: #6a7387;
        }
    }

    &__btn {
        display: flex;
        background: #00a92a;
        align-items: center;
        border: none;
        border-radius: 0 8px 8px 0;
        padding: 20px 15px !important;
        border-radius: 12px;
        cursor: pointer;
    }

    &-title {
        margin-bottom: 10px;
    }

    &__social-list {
        display: flex;
        gap: 10px;
        margin-bottom: 20px;
    }

    &__social-link {
        width: 48px;
        height: 48px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        background: #fff;
        border-radius: 8px;
        padding: 6px;
    }

    &__download {
        width: 100%;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        gap: 8px;
        background: #43b04a;
        border-radius: 8px;
        color: #fff;
        padding: 20px 10px !important;
        text-decoration: none;
    }

    &__bottom {
        border-top: 1px solid rgba(255, 255, 255, 0.1);
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding-top: 20px;
        font-size: 13px;
    }

    &__policy {
        display: flex;
        gap: 20px;
        list-style: none;
        padding: 0;
        margin: 0 auto 0 54px;

        a {
            &:hover {
                opacity: 1;
            }
        }
    }

    &__lang {
        display: flex;
        align-items: center;
        gap: 30px;
        font-size: 18px;
        letter-spacing: -0.01em;

        a {
            font-size: 18px;
            letter-spacing: -0.01em;
            color: #fff;
            border-right: 1px solid #fff;
            padding-right: 30px;
            color: #fff;
            text-decoration: none;

            &:last-child {
                border-right: 0;
                padding-right: 0;
            }

            &:hover {
                opacity: 1;
            }
        }
    }
}


.burger {
    display: none;
    position: relative;
    width: 30px;
    height: 22px;
    cursor: pointer;
    z-index: 1200;

    &__line,
    &__line::before,
    &__line::after {
        content: "";
        position: absolute;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: #111;
        transition: all 0.3s ease;
    }

    &__line {
        top: 50%;
        transform: translateY(-50%);
    }

    &__line::before {
        top: -8px;
    }

    &__line::after {
        top: 8px;
    }

    &.active .burger__line {
        background-color: transparent;
    }

    &.active .burger__line::before {
        top: 0;
        transform: rotate(45deg);
    }

    &.active .burger__line::after {
        top: 0;
        transform: rotate(-45deg);
    }
}

/* ======  ====== */
@media (max-width: 1460px) {
    .burger {
        display: block;
    }

    .header__right {
        margin-left: auto;
    }

    .header__language {
        margin-left: auto;
    }

    .header__btn {
        margin-right: 20px;
    }

    .header__menu {
        position: fixed;
        top: 0;
        right: -100%;
        width: 80%;
        max-width: 420px;
        height: 100vh;
        background: #fff;
        padding: 100px 40px 40px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        gap: 20px;
        transition: right 0.3s ease;
        z-index: 1100;
        box-shadow: -10px 0 25px rgba(0, 0, 0, 0.2);
    }

    .header__menu.menu--active {
        right: 0;
    }

    .header__menu-list {
        flex-direction: column;
        gap: 20px;
        align-items: baseline;
    }

    /*         */
    .header__language,
    .header__btn {
        display: flex;
    }

    /*      ,    */
    .header__menu .header__language,
    .header__menu .header__btn {
        display: none;
    }
}

/* ======   ====== */
@media (max-width: 556px) {

    /*       */
    .header__language,
    .header__btn {
        display: none;
    }

    /*      */
    .header__menu .header__language,
    .header__menu .header__btn {
        display: flex;
        // flex-direction: column;
        align-items: flex-start;
    }



    .header__menu .header__btn {
        margin-top: 20px;
        background-color: #00a92a;
        color: #fff;
        padding: 12px 20px;
        border-radius: 8px;
        text-align: center;
        width: 100%;
        font-weight: 600;
    }

    .header__menu .header__language {
        margin-top: auto;
        align-items: flex-end;
        gap: 8px;
        margin: auto auto 0 auto;
    }
    .header__logo{
        margin-right: auto;
    }
}